import React from 'react'
import Image from 'next/image'
import Logo from '@/assets/img/S.png'
import Pencil from '@/assets/svg/pencil.svg'
const Navbar = () => {
    return (
        <nav className='bg-red-500'>
            <div className="max-w-screen-lg m-auto">
                <div className='flex justify-between'>
                    <div className="logo">
                        <Image src={Logo} width={100} height={100} />
                    </div>
                    <ul className='flex items-center'>
                        <li><a className='mx-4 text-xl' href="">About</a></li>
                        <li><a className='mx-4 text-xl' href="">Project</a></li>
                        <li><a className='mx-4 text-xl' href="">Contact</a></li>
                    </ul>
                    <ul className='flex items-center'>
                        <li><a className='mx-4 text-xl' href="">Resume</a></li>
                        {/* <Image src={Pencil} width={100} height={100} /> */}
                    </ul>
                </div>
            </div>
        </nav>

    )
}

export default Navbar